<template>
	<div id="userInfo">
		<similarHeader  @goBack='goBack' @goMore='showPopup' :havaMore='true'></similarHeader>
		<div class="infoTop">
			<img :src="avator" alt="" @click='showImg=true'>
			<div class='userinfoArea'>
				<h2>
					{{nickName?nickName:userName}}
					<img :src="sex==='男'?nanUser:nvUser" class='userIcon'>
				</h2>
				<p v-show='nickName'>昵称：{{userName}}</p>
				<p>微信号：{{wechatNum}}</p>
				<p v-show='area'>地区：{{area}}</p>
			</div>
		</div>
		<ul class="infoMid">
			<li>
				设置备注和标签
			</li>
			<li class='borBottom' @click='goCircle'>
				<span>朋友圈</span>
				<div>
					<img :src="avator" alt="">
					<img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561713577592&di=b93d236f2c664a383ba5c44f2f133d1e&imgtype=0&src=http%3A%2F%2Fm.360buyimg.com%2Fmobilecms%2Fs1000x649_jfs%2Ft5644%2F171%2F2345819745%2F160970%2Fc199e614%2F592fa998Nbcb06230.jpg%2521q70.jpg" alt="">
				</div>
				
			</li>
			<li>
				更多信息
			</li>
		</ul>
		<div class="infoBottom">
			<p @click='goChat'><img src="~img/images/userInfo/infomsg.png" alt="" >发消息</p>
			<p v-show='wechatNum!="Maple_Story"' @click='showVideo'><img src="~img/images/userInfo/infovideo.png" alt="">音视频通话</p>
		</div>
		<van-popup v-model="showPop"  position="bottom" :style="{ height: '50%' }">
			<ul class='upop'>
				<li>
					<img src="~img/images/userInfo/pop-1.png" alt="" >
					<span>设置备注和标签</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-2.png" alt="" >
					<span>把他推荐给朋友</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-3.png" alt="" >
					<span>设为星标朋友</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-4.png" alt="" >
					<span>设置朋友圈和视频动态权限</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-5.png" alt="" >
					<span>加入黑名单</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-6.png" alt="" >
					<span>投诉</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-7.png" alt="" >
					<span>添加到桌面</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-8.png" alt="" >
					<span>删除</span>
				</li>
			</ul>
		</van-popup>
		<van-popup v-model="isVideo"  position="bottom" :style="{ height: '18%' }">
			<ul class='upop'>
				<li>
					<img src="~img/images/userInfo/pop-video.png" alt="" >
					<span>视频通话</span>
				</li>
				<li>
					<img src="~img/images/userInfo/pop-phone.png" alt="" >
					<span>语音通话</span>
				</li>
			</ul>	
		</van-popup>
		<van-image-preview
		  v-model="showImg"
		  :images="images"
		  :show-index='false'
		>
		</van-image-preview>
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{
				userName:'',
				wechatNum:'',
				nickName:'',
				avator:'',
				area:'',
				sex:'',
				showPop:false,
				isVideo:false,
				nanUser:require('img/images/userInfo/nan.png'),
				nvUser:require('img/images/userInfo/nv.png'),
				showImg: false,
			    images:[]
			    
			}
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			goChat(){
				sessionStorage.setItem('goHome',1);
				this.$router.push("/chat")
			},
			showPopup(){
				this.showPop = true;
			},
			showVideo(){
				this.isVideo=true;
			},
		    goCircle(){
		    	let wechatNum=sessionStorage.getItem('nowChatNum');
		    	if(wechatNum!=='Maple_Story'){
		    		this.$router.push('/othersCircle');
		    	}
		    	else{
		    		this.$router.push('/friendsCircle');
		    	}
		    }
		},
		activated(){
			this.images=[sessionStorage.getItem('nowUserAvator')];
			var nickName=sessionStorage.getItem('nowChatNickname');
			if(nickName==''){		
				this.nickName='';
			}
			else{
				this.nickName=nickName;
			}
			this.userName=sessionStorage.getItem('nowChatUser');
			this.avator=sessionStorage.getItem('nowUserAvator');
			this.wechatNum=sessionStorage.getItem('nowChatNum');
			this.sex=sessionStorage.getItem('nowUserSex');
			this.area=sessionStorage.getItem('nowUserArea');
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	#userInfo{
		background: #ededed;
		height:100vh;
	}
	header.similar{
		background: #fff;
		padding-top:.2rem;
	}
	header.similar .more{
		right: .4rem;
	}
	.infoTop{
		padding: .7rem 0 .5rem;
		display: flex;
		background: #fff;
		padding-left: .35rem;
		border-bottom: 1px solid rgba(233,233,233,.6);

	}
	.userinfoArea{
		width: 65%;
	}
	.userinfoArea>h2{
		font-size: .48rem;
		margin-bottom: .12rem;
		display: flex;
    	align-items: flex-start;
	}

	.userinfoArea>p{
		width: 86%;
		font-size: .34rem;
		margin-bottom: .1rem;
		color:#999;
	}
	.infoTop>img{
		width: 1.26rem;
		height: 1.26rem;
		margin-right: .4rem;
		border-radius: .12rem;
	}
	ul{
		background: #fff;
	    font-size: .38rem;
	}
	.infoMid li{
		font-size: .38rem;
		padding: .31rem 0;
		padding-left: .35rem;
	    background: #fff url('~img/images/right.png') no-repeat 96% center/.3rem;
	}
	.infoMid li:nth-child(1){
		  border-bottom: .2rem solid #ededed;
	}
	.infoMid li.borBottom{
		padding: .24rem 0;
		padding-left: 0;
		margin-left: .35rem;
		border-bottom: 1px solid rgba(233,233,233,.6);
		display: flex;
		align-items: center;
	}
	.infoMid li.borBottom>div{
		white-space: nowrap;
	    overflow: hidden;
	    width: 65%;
	    display: inline-block;
	}
	.infoMid li img{
		width: .85rem;
		height: .85rem;
		vertical-align: middle;
		margin-right: .12rem;
	}
	.infoMid li span{
		margin-right: .6rem;
	}
	.infoBottom{
		margin-top: .2rem;
	    background: #fff;
	    font-size: .38rem;
	    color: #586B94;
	}
	.infoBottom>p{
		display: flex;
		align-items: center;
		padding: .28rem 0;
		justify-content: center;
		font-weight:bold;
	}
	.infoBottom>p>img{
		width: .5rem;
    	height: .5rem;
    	margin-right: .2rem;
	}
	.infoBottom>p:first-child{
		border-bottom: 1px solid rgba(233,233,233,.6);
	}
	#userInfo >>> .van-overlay {
		background-color: rgba(0,0,0,.56);
	}
	#userInfo >>> .van-popup {
		transition: .13s;
		border-top-left-radius: .22rem;
    	border-top-right-radius: .22rem;
	}
	.upop{
		margin: .12rem 0;
		color:#636363;
	}
	.upop li{
		font-size: .36rem;
	    padding: .25rem;
	    display: flex;
	    margin-left: .3rem;
	    align-items: center;
	}
	.upop li img{
		width: .47rem;
		height: .47rem;
	}
	.upop li span{
		margin-left: .4rem;
	}
	.userIcon{
		height: 0.4rem;
	    width: 0.4rem;
	    margin-left: .1rem;
	}
	.van-image-preview {
		background: rgba(0,0,0,.9);
	}
</style>


